body.show-modal .backdrop.active {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-modal .backdrop.active .modal {
  transform: scale(1);
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 800;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .3);
  justify-content: center;
  align-items: center;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  max-width: 640px;
  width: 100%;
  min-height: 320px;
  text-align: center;
  background-color: #fff;
  border-radius: 4px;
  padding: 24px;
  transition: transform .3s ease-in-out;
  transform: scale(1.1);
}

.modal > p {
