.embla {
  max-width: 70rem;
  --slide-height: 60rem;
  --slide-spacing: 1rem;
  --slide-size: 100%;
  --slide-spacing-sm: 1.6rem;
  --slide-size-sm: 50%;
  --slide-spacing-lg: 5rem;
  --slide-size-lg: calc(100% / 4);
  margin: auto;
}

@media screen and (min-width: 1200px) {
  .embla {
    max-width: 170rem;
  }
}

.embla__viewport {
  overflow: hidden;
}

.embla__container {
  backface-visibility: hidden;
  touch-action: pan-y;
  margin-left: calc(var(--slide-spacing) * -1);
  display: flex;
}

@media tablet {
  .embla__container {
    margin-left: calc(var(--slide-spacing-sm) * -1);
  }
}

@media desktop {
  .embla__container {
    margin-left: calc(var(--slide-spacing-lg) * -1);
  }
}

.embla__slide {
  min-width: 0;
  flex: 0 0 var(--slide-size);
  padding-left: var(--slide-spacing);
}

@media (min-width: 750px) {
  .embla__slide {
    flex: 0 0 var(--slide-size-sm);
    padding-left: var(--slide-spacing-sm);
  }
}

@media (min-width: 1200px) {
  .embla__slide {
    flex: 0 0 var(--slide-size-lg);
    padding-left: var(--slide-spacing-lg);
  }
}

.embla__slide__number {
  box-shadow: inset 0 0 0 .2rem var(--detail-medium-contrast);
  height: var(--slide-height);
  border-radius: 1.8rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2.3rem;
  font-weight: 400;
  display: flex;
}

.embla__controls {
  grid-template-columns: auto 1fr;
  justify-content: space-between;
  gap: 1.2rem;
  margin-top: 1.8rem;
  display: grid;
}

.embla__buttons {
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  gap: .6rem;
  display: grid;
}

.embla__button {
  -webkit-tap-highlight-color: rgba(var(--text-high-contrast-rgb-value), .5);
  -ms-appearance: none;
  appearance: none;
  touch-action: manipulation;
  cursor: pointer;
  box-shadow: inset 0 0 0 .2rem var(--detail-medium-contrast);
  width: 3.6rem;
  height: 3.6rem;
  z-index: 1;
  color: var(--text-body);
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: flex;
}

.embla__button:disabled {
  color: var(--detail-high-contrast);
}

.embla__button__svg {
  width: 35%;
  height: 35%;
}

.embla__dots {
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  margin-right: -.6rem;
  display: flex;
}

.embla__dot {
  -webkit-tap-highlight-color: rgba(var(--text-high-contrast-rgb-value), .5);
  -ms-appearance: none;
  appearance: none;
  touch-action: manipulation;
  cursor: pointer;
  width: 2.6rem;
  height: 2.6rem;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: flex;
}

.embla__dot:after {
  box-shadow: inset 0 0 0 .2rem var(--detail-medium-contrast);
  width: 1.4rem;
  height: 1.4rem;
  content: "";
  border-radius: 50%;
  align-items: center;
  display: flex;
}

.embla__dot--selected:after {
  box-shadow: inset 0 0 0 .2rem var(--text-body);
}

